body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face{
	font-family: BebasNeue-Regular;
	src: url(/react-projects-test/static/media/BebasNeue-Regular.47f711bd.ttf);
	font-weight: bold;
}
@font-face{
	font-family: Montserrat-Light;
	src: url(/react-projects-test/static/media/Montserrat-Light.57a8d90f.ttf);
	font-weight: bold;
}
@font-face{
	font-family: Montserrat-Regular;
	src: url(/react-projects-test/static/media/Montserrat-Regular.68900018.ttf);
	font-weight: bold;
}
@font-face{
	font-family: Montserrat-Bold;
	src: url(/react-projects-test/static/media/Montserrat-Bold.0c12ee2f.ttf);
