@font-face{
	font-family: TrendSansOne;
	src: url(/react-projects-test/static/media/TrendSansOne.718651c5.ttf);
	font-weight: bold;
}
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: TrendSansOne;
}

.Header ul {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 0;
    grid-column-gap: 15px;
    -webkit-column-gap: 15px;
            column-gap: 15px;
}
.Header {
    background-color: #f0ebe7;
}
.Header a {
    font-size: 15px;
    color: #4c4557;
    text-decoration: none;
}
.Header a:hover{
    color: #000;
}
.Header a li{
    list-style: none;
}
.Header .ni{
    border-radius: 20px;
    padding: 10px;
    transition-duration: 0.2s;
}
.Header .ni:hover{
    background-color: #0000001c;
    transition-duration: 0.2s;
}
